___CSS_LOADER_EXPORT___.push([module.id, `/* src/webview/styles/flow-graph.css */

.flow-graph-container {
  width: 100vw;
  height: 100vh;
}

.flow-graph-panel {
  display: flex;
  gap: 8px;
  background: var(--vscode-editor-background);
  border: 1px solid var(--vscode-panel-border);
  border-radius: 8px;
  padding: 8px;
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1);
}

.flow-graph-button {
  border: none;
  padding: 8px 12px;
  border-radius: 4px;
  cursor: pointer;
  font-size: 16px;
  transition: all 0.2s ease;
  min-width: 36px;
  height: 36px;
}

.flow-graph-button:hover {
  transform: scale(1.05);
}

.flow-graph-button:active {
  transform: scale(0.95);
}

.flow-graph-button-primary {
  background: var(--vscode-button-background);
  color: var(--vscode-button-foreground);
}

.flow-graph-button-primary:hover {
  background: var(--vscode-button-hoverBackground);
}

.flow-graph-button-toggle-on {
  background: var(--vscode-button-background);
  color: var(--vscode-button-foreground);
}

.flow-graph-button-toggle-off {
  background: #6b7280;
  color: #d1d5db;
}`, "",{"version":3,"sources":["webpack://./src/webview/styles/flow-graph.css"],"names":[],"mappings":"AAAA,sCAAsC;;AAEtC;EACE,YAAY;EACZ,aAAa;AACf;;AAEA;EACE,aAAa;EACb,QAAQ;EACR,2CAA2C;EAC3C,4CAA4C;EAC5C,kBAAkB;EAClB,YAAY;EACZ,6CAA6C;AAC/C;;AAEA;EACE,YAAY;EACZ,iBAAiB;EACjB,kBAAkB;EAClB,eAAe;EACf,eAAe;EACf,yBAAyB;EACzB,eAAe;EACf,YAAY;AACd;;AAEA;EACE,sBAAsB;AACxB;;AAEA;EACE,sBAAsB;AACxB;;AAEA;EACE,2CAA2C;EAC3C,sCAAsC;AACxC;;AAEA;EACE,gDAAgD;AAClD;;AAEA;EACE,2CAA2C;EAC3C,sCAAsC;AACxC;;AAEA;EACE,mBAAmB;EACnB,cAAc;AAChB","sourcesContent":["/* src/webview/styles/flow-graph.css */\n\n.flow-graph-container {\n  width: 100vw;\n  height: 100vh;\n}\n\n.flow-graph-panel {\n  display: flex;\n  gap: 8px;\n  background: var(--vscode-editor-background);\n  border: 1px solid var(--vscode-panel-border);\n  border-radius: 8px;\n  padding: 8px;\n  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1);\n}\n\n.flow-graph-button {\n  border: none;\n  padding: 8px 12px;\n  border-radius: 4px;\n  cursor: pointer;\n  font-size: 16px;\n  transition: all 0.2s ease;\n  min-width: 36px;\n  height: 36px;\n}\n\n.flow-graph-button:hover {\n  transform: scale(1.05);\n}\n\n.flow-graph-button:active {\n  transform: scale(0.95);\n}\n\n.flow-graph-button-primary {\n  background: var(--vscode-button-background);\n  color: var(--vscode-button-foreground);\n}\n\n.flow-graph-button-primary:hover {\n  background: var(--vscode-button-hoverBackground);\n}\n\n.flow-graph-button-toggle-on {\n  background: var(--vscode-button-background);\n  color: var(--vscode-button-foreground);\n}\n\n.flow-graph-button-toggle-off {\n  background: #6b7280;\n  color: #d1d5db;\n}"],"sourceRoot":""}]);
